import React, { useEffect, useMemo, useState } from 'react';
import styled from '@emotion/styled';
import ExpandableCard from '@leafygreen-ui/expandable-card';
import { Option, Select } from '@leafygreen-ui/select';

import { useAtlas } from '../../realm';
import { PlayerMatch, PlayerMatchup, Season, Team, TeamMatch, TeamMatchup } from '../../types';

import Match from './match';

const StyledMain = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  justify-content: center;
`;

const StyledSelects = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
`;

const Matches = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  justify-content: center;
  max-width: 720px;
`;

const MatchRound = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 12px;
`;

interface Props {
  seasons: Season[];
  teams: Team[];
}

function TeamPage({ seasons, teams }: Props) {
  const { data } = useAtlas();

  const [selectedSeason, setSelectedSeason] = useState<Season>();
  const [selectedTeam, setSelectedTeam] = useState<Team>();

  const team = useMemo(() => teams.find(t => t.name === selectedTeam?.name), [selectedTeam, teams]);

  const [teamMatchesById, setTeamMatchesById] = useState<Record<string, TeamMatch>>({});
  const [playerMatchesById, setPlayerMatchesById] = useState<Record<string, PlayerMatch[]>>({});
  useEffect(() => {
    if (!data) {
      return;
    }

    Promise.all([
      data.teamMatches().find({
        season_xref: selectedSeason?.xref,
        'teams.xref': selectedTeam?.xref,
      }),
      data.playerMatches().find({
        season_xref: selectedSeason?.xref,
        'players.team.xref': selectedTeam?.xref,
      }),
    ]).then(([teamMatches, playerMatches]) => {
      setTeamMatchesById(
        teamMatches.reduce<Record<number, TeamMatch>>((acc, teamMatch) => ({ ...acc, [teamMatch._id]: teamMatch }), {}),
      );

      const playerMatchesById = playerMatches.reduce<Record<string, PlayerMatch[]>>(
        (acc, playerMatch) => ({
          ...acc,
          [playerMatch.match_id]: [...(acc[playerMatch.match_id] || []), playerMatch],
        }),
        {},
      );
      Object.entries(playerMatchesById).forEach(([teamMatchId, playerMatches]) => {
        playerMatches.sort((m1, m2) => m1.seq - m2.seq);
        playerMatchesById[teamMatchId] = playerMatches;
      });
      setPlayerMatchesById(playerMatchesById);
    });
  }, [data, selectedSeason?.xref, selectedTeam?.xref, team?.night]);

  if (!data) {
    return null;
  }

  return (
    <StyledMain>
      <StyledSelects>
        <Select
          style={{ width: '360px' }}
          label="Choose a season:"
          placeholder="Select..."
          onChange={val => setSelectedSeason(seasons.find(s => s.name === val))}
        >
          {seasons.map(({ xref, name }) => (
            <Option key={xref} value={name}>
              {name}
            </Option>
          ))}
        </Select>
        <Select
          style={{ width: '360px' }}
          label="Choose a team:"
          placeholder="Select..."
          disabled={!selectedSeason}
          onChange={val => setSelectedTeam(teams.find(t => t.name === val))}
        >
          {teams
            .filter(team => team.season_xref === selectedSeason?.xref)
            .map(({ xref, name }) => (
              <Option key={xref} value={name}>
                {name}
              </Option>
            ))}
        </Select>
      </StyledSelects>
      {selectedTeam && (
        <Matches>
          {(team?.schedule || []).map(({ display, match_id, opp }) => {
            const teamMatchup = newTeamMatchup(teamMatchesById[match_id || ''] || { teams: [] }, selectedTeam?.xref);

            const [match11, match12, match21, match22, ...tb] = playerMatchesById[match_id || ''] || [];

            const matchup11 = newPlayerMatchup(match11 || { players: [] }, selectedTeam?.xref);
            const matchup12 = newPlayerMatchup(match12 || { players: [] }, selectedTeam?.xref);
            const matchup21 = newPlayerMatchup(match21 || { players: [] }, selectedTeam?.xref);
            const matchup22 = newPlayerMatchup(match22 || { players: [] }, selectedTeam?.xref);
            const matchupTb = !tb.length ? undefined : newPlayerMatchup(tb[0], selectedTeam?.xref);

            return (
              <ExpandableCard
                key={display}
                title={`${display}: vs. ${opp.name}`}
                description={!match_id ? '' : matchDescription(teamMatchup)}
              >
                {!match_id && <>this match hasn't happened yet, do you want a scouting report?</>}
                {match_id && (
                  <>
                    <MatchRound>
                      <Match matchup={matchup11} />
                      <Match matchup={matchup12} />
                    </MatchRound>
                    <MatchRound>
                      <Match matchup={matchup21} />
                      <Match matchup={matchup22} />
                    </MatchRound>
                    {(matchupTb && <Match matchup={matchupTb} />) || null}
                  </>
                )}
              </ExpandableCard>
            );
          })}
        </Matches>
      )}
    </StyledMain>
  );
}

export default TeamPage;

function matchDescription({ self, opp }: TeamMatchup) {
  return `${self.win ? 'W' : 'L'}: ${self.games_won} - ${opp.games_won} (${self.matches_won} - ${opp.matches_won})`;
}

function newTeamMatchup(teamMatch: Pick<TeamMatch, 'teams'>, selectedTeamXref?: number) {
  return teamMatch.teams.reduce<TeamMatchup>(
    (acc, team) => {
      if (selectedTeamXref === team.xref) {
        return { ...acc, self: team };
      }
      return { ...acc, opp: team };
    },
    {
      self: { xref: 0, name: '', games_won: 0, matches_won: 0 },
      opp: { xref: 0, name: '', games_won: 0, matches_won: 0 },
    },
  );
}

function newPlayerMatchup(playerMatch: Pick<PlayerMatch, 'players'>, selectedTeamXref?: number) {
  return playerMatch.players.reduce<PlayerMatchup>(
    (acc, player) => {
      if (selectedTeamXref === player.team.xref) {
        return { ...acc, self: player };
      }
      return { ...acc, opp: player };
    },
    {
      self: { xref: 0, name: '', team: { xref: 0, name: '' }, handicap: 0, games_won: 0 },
      opp: { xref: 0, name: '', team: { xref: 0, name: '' }, handicap: 0, games_won: 0 },
    },
  );
}
