import React, { useMemo } from 'react';
import { Table, TableHeader, Row, Cell } from '@leafygreen-ui/table';

import { emptyPlayerResult, PlayerMatch, PlayerResult } from '../../types';

interface Props {
  playerMatches: PlayerMatch[];
  selfTeamXref: number;
}

interface Stats extends Pick<PlayerResult, 'xref' | 'name' | 'handicap'> {
  doubles: number;
  wins: number;
  losses: number;
  gamesWon: number;
  gamesLost: number;
  gamePct: number;
}

const emptyStats = (): Stats => ({
  xref: 0,
  name: '',
  handicap: 0,
  doubles: 0,
  wins: 0,
  losses: 0,
  gamesWon: 0,
  gamesLost: 0,
  gamePct: 0,
});

function PlayerResults({ playerMatches, selfTeamXref }: Props) {
  const playerStatsByWeek = useMemo(
    () =>
      playerMatches.reduce<Record<number, Record<number, Stats[]>>>((acc, playerMatch) => {
        const self = playerMatch.players.find(p => p.team.xref === selfTeamXref) || emptyPlayerResult();
        const opp = playerMatch.players.find(p => p.team.xref !== selfTeamXref) || emptyPlayerResult();
        return {
          ...acc,
          [self.xref]: {
            ...(acc[self.xref] || {}),
            [playerMatch.week_num]: [
              ...((acc[self.xref] || {})[playerMatch.week_num] || []),
              {
                xref: self.xref,
                name: self.name,
                handicap: self.handicap,
                wins: self.win ? 1 : 0,
                losses: self.loss ? 1 : 0,
                gamesWon: self.games_won,
                gamesLost: opp.games_won,
                doubles: 0,
                gamePct: 0,
              },
            ],
          },
        };
      }, {}),
    [playerMatches, selfTeamXref],
  );

  const playerStats = useMemo(() => {
    const playerStats = Object.entries(playerStatsByWeek).map(([xref, statsByWeek]) => {
      const stats = emptyStats();
      stats.xref = parseInt(xref, 10);

      Object.values(statsByWeek).forEach(weeklyStats => {
        const isDouble = weeklyStats.length > 1;
        for (var i = 0; i < weeklyStats.length; i++) {
          const { name, handicap, wins, losses, gamesWon, gamesLost } = weeklyStats[i];
          stats.name = name;
          stats.handicap = handicap;
          stats.wins += wins;
          stats.losses += losses;
          stats.gamesWon += gamesWon;
          stats.gamesLost += gamesLost;
          stats.doubles = stats.doubles + (isDouble ? 1 : 0);
        }
      });

      stats.gamePct = stats.gamesWon / (stats.gamesWon + stats.gamesLost);
      return stats;
    });
    playerStats.sort((p1, p2) => p2.gamePct - p1.gamePct);
    return playerStats;
  }, [playerStatsByWeek]);

  return (
    <Table
      data={playerStats}
      columns={[
        <TableHeader label="Name" />,
        <TableHeader label="HCP" />,
        <TableHeader label="DB's" />,
        <TableHeader label="W" />,
        <TableHeader label="L" />,
        <TableHeader label="GF" />,
        <TableHeader label="GA" />,
        <TableHeader label="PCT" />,
      ]}
    >
      {({ datum: pr }) => (
        <Row key={pr.xref}>
          <Cell>{pr.name}</Cell>
          <Cell>{pr.handicap}</Cell>
          <Cell>{pr.doubles}</Cell>
          <Cell>{pr.wins}</Cell>
          <Cell>{pr.losses}</Cell>
          <Cell>{pr.gamesWon}</Cell>
          <Cell>{pr.gamesLost}</Cell>
          <Cell>{pr.gamePct.toFixed(3)}</Cell>
        </Row>
      )}
    </Table>
  );
}

export default PlayerResults;
