import React, { useMemo } from 'react';
import { Link } from 'react-router-dom';
import styled from '@emotion/styled';
import ExpandableCard from '@leafygreen-ui/expandable-card';
import Modal from '@leafygreen-ui/modal';
import { H2, H3 } from '@leafygreen-ui/typography';

import { PlayerMatch, Team, TeamMatch, TeamMatchup, TeamResult } from '../../types';

import MatchTable from './match_table';
import PlayerResults from './player_results';

const emptyTeamResult = (): TeamResult => ({ xref: 0, name: '', games_won: 0, matches_won: 0 });

const Matches = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  justify-content: center;
  max-width: 720px;
`;

interface Props {
  open: boolean;
  playerMatches: PlayerMatch[];
  team?: Team;
  teamMatches: TeamMatch[];
  setOpen: (open: boolean) => void;
}

function TeamResults({ open, playerMatches, setOpen, team, teamMatches }: Props) {
  const teamMatchesById = useMemo(
    () =>
      teamMatches.reduce<Record<string, TeamMatch>>((acc, teamMatch) => ({ ...acc, [teamMatch._id]: teamMatch }), {}),
    [teamMatches],
  );

  const playerMatchesById = useMemo(() => {
    const playerMatchesById = playerMatches.reduce<Record<string, PlayerMatch[]>>(
      (acc, playerMatch) => ({
        ...acc,
        [playerMatch.match_id]: [...(acc[playerMatch.match_id] || []), playerMatch],
      }),
      {},
    );
    Object.entries(playerMatchesById).forEach(([teamMatchId, playerMatches]) => {
      playerMatches.sort((m1, m2) => m1.seq - m2.seq);
      playerMatchesById[teamMatchId] = playerMatches;
    });
    return playerMatchesById;
  }, [playerMatches]);

  if (!team) {
    return null;
  }

  return (
    <Modal open={open} setOpen={setOpen}>
      <>
        <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
          <H2>{team.name}</H2>
          <Link to={team.name}>Detailed View</Link>
        </div>
        <H3 style={{ marginTop: '1rem' }}>Player Results</H3>
        <PlayerResults playerMatches={playerMatches} selfTeamXref={team.xref} />
        <H3 style={{ marginTop: '1rem' }}>Team Results</H3>
        <Matches>
          {team.schedule.map(({ display, match_id, opp }) => {
            const teamMatchup = newTeamMatchup(team.xref, teamMatchesById[match_id || ''] || { teams: [] });
            return (
              <ExpandableCard
                key={display}
                title={`${display}: vs. ${opp.name}`}
                description={!match_id ? '' : matchDescription(teamMatchup)}
              >
                {!match_id && <>this match hasn't happened yet, do you want a scouting report?</>}
                {match_id && (
                  <MatchTable playerMatches={playerMatchesById[match_id || ''] || []} selfTeamXref={team.xref} />
                )}
              </ExpandableCard>
            );
          })}
        </Matches>
      </>
    </Modal>
  );
}

export default TeamResults;

function matchDescription({ self, opp }: TeamMatchup) {
  return `${self.win ? ' Win' : ' Loss'}: ${self.matches_won}-${opp.matches_won}, ${self.games_won}-${
    opp.games_won
  }`;
}

function newTeamMatchup(selectedTeamXref: number, teamMatch: Pick<TeamMatch, 'teams'>): TeamMatchup {
  const self = teamMatch.teams.find(t => t.xref === selectedTeamXref) || emptyTeamResult();
  const opp = teamMatch.teams.find(t => t.xref !== selectedTeamXref) || emptyTeamResult();
  return { self, opp };
}
