import React from 'react';
import { useTheme } from '@emotion/react';
import styled from '@emotion/styled';
import Card from '@leafygreen-ui/card';
import { Body, Subtitle } from '@leafygreen-ui/typography';

import { PlayerMatchup, PlayerResult } from '../../types';

const StyledMatch = styled<any>(Card)`
  width: 328px;
  display: flex;
  justify-content: space-between;
  background-color: ${({ bg }) => bg};
  ${({ first }) => !first && { marginTop: '1rem' }}
`;

const Player = styled.div`
  display: flex;
  flex-direction: column;
`;

interface Props {
  matchup: PlayerMatchup;
  first?: boolean;
}

function Match({ first, matchup }: Props) {
  const theme = useTheme();
  return (
    <StyledMatch first={first} bg={matchup.self.win ? theme.colors.lg.green.light2 : theme.colors.lg.red.light2}>
      <Player>
        <Subtitle>{matchup.self.name}</Subtitle>
        <Body>{`Handicap: ${matchup.self.handicap}`}</Body>
        <Body>{`Games Won: ${matchup.self.games_won}`}</Body>
      </Player>
      <Player>
        <Subtitle>{matchup.opp.name}</Subtitle>
        <Body>{`Handicap: ${matchup.opp.handicap}`}</Body>
        <Body>{`Games Won: ${matchup.opp.games_won}`}</Body>
      </Player>
    </StyledMatch>
  );
}

export default Match;
