import React, { useEffect, useMemo, useState } from 'react';
import { useParams } from 'react-router-dom';
import { Routes, Route } from 'react-router-dom';
import Banner from '@leafygreen-ui/banner';

import { PageContent } from '../../components';
import { PlayerMatch, Season, Team, TeamMatch } from '../../types';

import Admin from './season_admin';
import Standings from './season_standings';

interface Props {
  seasons: Season[];
  loadPlayerMatches: (seasonXref: number) => Promise<PlayerMatch[]>;
  loadTeams: (seasonXref: number) => Promise<Team[]>;
  loadTeamMatches: (seasonXref: number) => Promise<TeamMatch[]>;
  saveDivisions: (seasonXref: number, divisions: Season['divisions']) => Promise<void>;
  saveTeams: (seasonXref: number, teams: Omit<Team, '_id' | 'season_xref' | 'schedule'>[]) => Promise<void>;
}

function SeasonPage({ seasons, loadPlayerMatches, loadTeams, loadTeamMatches, saveDivisions, saveTeams }: Props) {
  const { year, term, type } = useParams();

  const season = useMemo(() => {
    return seasons.find(s => s.year + '' === year && s.term === term && s.type === type);
  }, [seasons, year, term, type]);

  const [teams, setTeams] = useState<Team[]>([]);
  useEffect(() => {
    if (!season) {
      return;
    }
    loadTeams(season.xref).then(setTeams);
  }, [loadTeams, season]);

  const [teamMatches, setTeamMatches] = useState<TeamMatch[]>([]);
  useEffect(() => {
    if (!season) {
      return;
    }
    loadTeamMatches(season.xref).then(setTeamMatches);
  }, [loadTeamMatches, season]);

  const [playerMatches, setPlayerMatches] = useState<PlayerMatch[]>([]);
  useEffect(() => {
    if (!season) {
      return;
    }
    loadPlayerMatches(season.xref).then(setPlayerMatches);
  }, [loadPlayerMatches, season]);

  if (!season) {
    return (
      <PageContent>
        <Banner variant="danger">The specified season does not exist, please check your URL.</Banner>
      </PageContent>
    );
  }
  return (
    <Routes>
      <Route
        path="/"
        element={<Standings season={season} playerMatches={playerMatches} teams={teams} teamMatches={teamMatches} />}
      />
      <Route
        path="admin"
        element={
          <Admin
            season={season}
            teams={teams}
            saveDivisions={divisions => saveDivisions(season.xref, divisions)}
            saveTeams={teams => saveTeams(season.xref, teams)}
          />
        }
      />
    </Routes>
  );
}

export default SeasonPage;
