import React, { useEffect, useState } from 'react';
import styled from '@emotion/styled';
import { Option, Select } from '@leafygreen-ui/select';
import { Table, TableHeader, Row, Cell } from '@leafygreen-ui/table';
import { H2, H3 } from '@leafygreen-ui/typography';

import { useAtlas } from '../../realm';
import { Season, Team, TeamRecord } from '../../types';

const StyledMain = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  justify-content: center;
`;

const Leagues = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
`;

const Divisions = styled.div`
  display: flex;
  flex-direction: column;
  margin: 0 1rem;
`;

const Division = styled.div`
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
`;

interface Props {
  seasons: Season[];
}

interface MatchStats {
  wins: number;
  losses: number;
  gamesWon: number;
  gamesTotal: 0;
}

function StandingsPage({ seasons }: Props) {
  const { data } = useAtlas();

  const [selectedSeason, setSelectedSeason] = useState<Season>();

  const [teamsByDivision, setTeamsByDivision] = useState<Record<string, TeamRecord[]>>({});
  useEffect(() => {
    if (!data || !selectedSeason) {
      return;
    }
    Promise.all([
      data.teamMatches().find({ season_xref: selectedSeason.xref }),
      data.teams().find({ season_xref: selectedSeason.xref }),
    ]).then(([teamMatches, teams]) => {
      const teamResults = teamMatches.reduce<Record<number, MatchStats>>((acc, teamMatch) => {
        const leftTeam = acc[teamMatch.teams[0].xref] || { wins: 0, losses: 0, gamesWon: 0, gamesTotal: 0 };
        // teamMatch.left_team.win ? leftTeam.wins++ : leftTeam.losses++;
        leftTeam.gamesWon += teamMatch.teams[0].games_won;
        leftTeam.gamesTotal += teamMatch.teams[0].games_won + teamMatch.teams[1].games_won;

        const rightTeam = acc[teamMatch.teams[1].xref] || { wins: 0, losses: 0, gamesWon: 0, gamesTotal: 0 };
        // teamMatch.right_team.win ? rightTeam.wins++ : rightTeam.losses++;
        rightTeam.gamesWon += teamMatch.teams[1].games_won;
        rightTeam.gamesTotal += teamMatch.teams[1].games_won + teamMatch.teams[0].games_won;

        if (teamMatch.teams[0].win) {
          leftTeam.wins++;
          rightTeam.losses++;
        } else if (teamMatch.teams[1].win) {
          leftTeam.losses++;
          rightTeam.wins++;
        }

        return {
          ...acc,
          [teamMatch.teams[0].xref]: leftTeam,
          [teamMatch.teams[1].xref]: rightTeam,
        };
      }, {});

      const teamsByDivision = teams.reduce<Record<string, Team[]>>(
        (acc, team) => ({
          ...acc,
          [team.division_name]: [...(acc[team.division_name] || []), team],
        }),
        {},
      );

      setTeamsByDivision(
        Object.entries(teamsByDivision).reduce((acc, [division, teams]) => {
          const standings = teams.map(team => {
            const results = teamResults[team.xref];
            return {
              ...team,
              rank: 0,
              wins: results.wins,
              losses: results.losses,
              pct: results.gamesWon / results.gamesTotal,
            };
          });

          standings.sort((s1, s2) =>
            s1.wins === s2.wins
              ? s1.losses === s2.losses
                ? s2.pct - s1.pct
                : s1.losses - s2.losses
              : s2.wins - s1.wins,
          );

          return {
            ...acc,
            [division]: standings.map((s, i) => ({ ...s, rank: i + 1 })),
          };
        }, {}),
      );
    });
  }, [data, selectedSeason]);

  if (!data) {
    return null;
  }

  return (
    <StyledMain>
      <Select
        style={{ width: '360px' }}
        label="Choose a season:"
        placeholder="Select..."
        value={selectedSeason?.name || ''}
        onChange={val => setSelectedSeason(seasons.find(season => season.name === val))}
      >
        {seasons.map(({ xref, name }) => (
          <Option key={xref} value={name}>
            {name}
          </Option>
        ))}
      </Select>
      {selectedSeason && (
        <Leagues>
          {selectedSeason.nights.map(night => (
            <Divisions key={night}>
              <H2 style={{ marginBottom: '1rem' }}>{night}</H2>
              {selectedSeason.divisions[night]!.map(division => (
                <Division key={division}>
                  {teamsByDivision[division] && (
                    <>
                      <H3>{division}</H3>
                      <Table
                        data={teamsByDivision[division]}
                        columns={[
                          <TableHeader label="Place" />,
                          <TableHeader label="Team Name" />,
                          <TableHeader label="W's" />,
                          <TableHeader label="L's" />,
                          <TableHeader label="PCT." />,
                        ]}
                      >
                        {({ datum: ts }) => (
                          <Row key={ts.xref}>
                            <Cell>{ts.rank}</Cell>
                            <Cell>{ts.name}</Cell>
                            <Cell>{ts.wins}</Cell>
                            <Cell>{ts.losses}</Cell>
                            <Cell>{ts.pct.toFixed(3)}</Cell>
                          </Row>
                        )}
                      </Table>
                    </>
                  )}
                </Division>
              ))}
            </Divisions>
          ))}
        </Leagues>
      )}
    </StyledMain>
  );
}

export default StandingsPage;
