import React, { useEffect, useState } from 'react';
import styled from '@emotion/styled';
import { Option, Select } from '@leafygreen-ui/select';
// import { Table, TableHeader, Row, Cell } from '@leafygreen-ui/table';

import { useAtlas } from '../../realm';
import { Player, Season, Team } from '../../types';

interface WinLossRecord {
  wins: number;
  losses: number;
  total: number;
}

interface PlayerResult {
  key: string;
  name: string;
  teamName: string;
  handicap: number;
  doubles: number;
  games: WinLossRecord;
  matches: WinLossRecord;
}

interface TeamWeekly {
  name: string;
  totalGames: WinLossRecord;
  totalMatches: WinLossRecord;
  weeklyWins: number;
  weeklyLosses: number;
}

const StyledMain = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  justify-content: center;
`;

interface Props {
  seasons: Season[];
  players: Player[];
  teams: Team[];
}

function PlayersPage({ seasons }: Props) {
  const { data } = useAtlas();

  const [selectedSeason, setSelectedSeason] = useState<Season>();

  const [teamResults, setTeamResults] = useState<TeamWeekly[]>([]);
  const [playerResults, setPlayerResults] = useState<PlayerResult[]>([]);
  useEffect(() => {
    if (!data) {
      return;
    }

    Promise.all([
      data.teamMatches().find({ season_xref: selectedSeason?.xref }),
      data.playerMatches().find({ season_xref: selectedSeason?.xref }),
    ]).then(([teamMatches, playerMatches]) => {
      setTeamResults(
        Object.values(
          Object.values(
            teamMatches.reduce<Record<string, TeamWeekly>>((acc, teamMatch) => {
              const leftTeam: TeamWeekly = acc[teamMatch.teams[0].name] || {
                name: teamMatch.teams[0].name,
                totalGames: { wins: 0, losses: 0, total: 0 },
                totalMatches: { wins: 0, losses: 0, total: 0 },
                weeklyWins: 0,
                weeklyLosses: 0,
              };
              const rightTeam: TeamWeekly = acc[teamMatch.teams[1].name] || {
                name: teamMatch.teams[1].name,
                totalGames: { wins: 0, losses: 0, total: 0 },
                totalMatches: { wins: 0, losses: 0, total: 0 },
                weeklyWins: 0,
                weeklyLosses: 0,
              };
              return {
                ...acc,
                [teamMatch.teams[0].name]: {
                  ...leftTeam,
                  totalGames: {
                    wins: leftTeam.totalGames.wins + teamMatch.teams[0].games_won,
                    losses: leftTeam.totalGames.losses + teamMatch.teams[1].games_won,
                    total: leftTeam.totalGames.total + teamMatch.teams[0].games_won + teamMatch.teams[1].games_won,
                  },
                  totalMatches: {
                    wins: leftTeam.totalMatches.wins + teamMatch.teams[0].matches_won,
                    losses: leftTeam.totalMatches.losses + teamMatch.teams[1].matches_won,
                    total:
                      leftTeam.totalMatches.total + teamMatch.teams[0].matches_won + teamMatch.teams[1].matches_won,
                  },
                  weeklyWins: leftTeam.weeklyWins + (teamMatch.teams[0].win ? 1 : 0),
                  weeklyLosses: leftTeam.weeklyWins + (teamMatch.teams[0].loss ? 1 : 0),
                },
                [teamMatch.teams[1].name]: {
                  ...rightTeam,
                  totalGames: {
                    wins: rightTeam.totalGames.wins + teamMatch.teams[1].games_won,
                    losses: rightTeam.totalGames.losses + teamMatch.teams[0].games_won,
                    total: rightTeam.totalGames.total + teamMatch.teams[1].games_won + teamMatch.teams[0].games_won,
                  },
                  totalMatches: {
                    wins: rightTeam.totalMatches.wins + teamMatch.teams[1].matches_won,
                    losses: rightTeam.totalMatches.losses + teamMatch.teams[0].matches_won,
                    total:
                      rightTeam.totalMatches.total + teamMatch.teams[1].matches_won + teamMatch.teams[0].matches_won,
                  },
                  weeklyWins: rightTeam.weeklyWins + (teamMatch.teams[1].win ? 1 : 0),
                  weeklyLosses: rightTeam.weeklyWins + (teamMatch.teams[1].loss ? 1 : 0),
                },
              };
            }, {}),
          ),
        ),
      );

      const pm = playerMatches.reduce(
        (acc, playerMatch) => ({
          ...acc,
        }),
        {},
      );
      console.log(pm);
    });
  }, [data, selectedSeason?.xref]);

  if (!data) {
    console.log(teamResults, playerResults, setPlayerResults);
    return null;
  }

  return (
    <StyledMain>
      <Select
        style={{ width: '360px' }}
        label="Choose a season:"
        placeholder="Select..."
        value={selectedSeason?.name || ''}
        onChange={val => setSelectedSeason(seasons.find(season => season.name === val))}
      >
        {seasons.map(({ xref, name }) => (
          <Option key={xref} value={name}>
            {name}
          </Option>
        ))}
      </Select>
      {selectedSeason && <>player data</>}
    </StyledMain>
  );
}

export default PlayersPage;
