import React, { useEffect, useMemo, useState } from 'react';
import { useTheme } from '@emotion/react';
import styled from '@emotion/styled';
import Button from '@leafygreen-ui/button';
import Modal from '@leafygreen-ui/modal';
import { Table, TableHeader, Row, Cell } from '@leafygreen-ui/table';
import { Body, H2, H3 } from '@leafygreen-ui/typography';

import { dayOfWeek, PlayerMatch, Season, Team, TeamMatch, TeamRecord } from '../../types';

import TeamResults from './team_results';

const Leagues = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
`;

const Divisions = styled.div`
  display: flex;
  flex-direction: column;
  margin: 0 1rem;
`;

const Division = styled.div`
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
`;

interface Props {
  season: Season;
  playerMatches: PlayerMatch[];
  teams: Team[];
  teamMatches: TeamMatch[];
}

interface MatchStats {
  wins: number;
  losses: number;
  gamesWon: number;
  gamesTotal: number;
}

const emptyMatchStats = () => ({ wins: 0, losses: 0, gamesWon: 0, gamesTotal: 0 });

function SeasonPage({ playerMatches, season, teams, teamMatches }: Props) {
  const theme = useTheme();

  const teamsByXref = useMemo(
    () => teams.reduce<Record<number, Team>>((acc, team) => ({ ...acc, [team.xref]: team }), {}),
    [teams],
  );

  const seasonResults = useMemo(
    () =>
      teamMatches.reduce<Record<number, MatchStats>>((outer, teamMatch) => {
        var gamesTotal = 0;
        const matchStats = teamMatch.teams.reduce<Record<number, MatchStats>>(
          (inner, team) => {
            gamesTotal += team.games_won;
            return {
              ...inner,
              [team.xref]: {
                gamesWon: inner[team.xref].gamesWon + team.games_won,
                wins: inner[team.xref].wins + (team.win ? 1 : 0),
                losses: inner[team.xref].losses + (team.loss ? 1 : 0),
                gamesTotal: inner[team.xref].gamesTotal,
              },
            };
          },
          {
            [teamMatch.teams[0].xref]: outer[teamMatch.teams[0].xref] || emptyMatchStats(),
            [teamMatch.teams[1].xref]: outer[teamMatch.teams[1].xref] || emptyMatchStats(),
          },
        );
        Object.values(matchStats).forEach(stats => {
          stats.gamesTotal += gamesTotal;
        });
        return {
          ...outer,
          ...matchStats,
        };
      }, {}),
    [teamMatches],
  );

  const teamsByDivision = useMemo(() => {
    const teamRecords = teams.reduce<Record<string, TeamRecord[]>>((acc, team) => {
      const teamResults = seasonResults[team.xref] || emptyMatchStats();
      return {
        ...acc,
        [team.division_name]: [
          ...(acc[team.division_name] || []),
          {
            xref: team.xref,
            name: team.name,
            rank: 0,
            wins: teamResults.wins,
            losses: teamResults.losses,
            pct: teamResults.gamesWon / teamResults.gamesTotal,
          },
        ],
      };
    }, {});
    Object.entries(teamRecords).forEach(([divisionName, records]) => {
      records.sort((r1, r2) =>
        r1.wins === r2.wins ? (r1.losses === r2.losses ? r2.pct - r1.pct : r1.losses - r2.losses) : r2.wins - r1.wins,
      );
      teamRecords[divisionName] = records.map((tr, i) => ({ ...tr, rank: i + 1 }));
    });
    return teamRecords;
  }, [teams]);

  const [modalTeam, setModalTeam] = useState(0);
  const [modalTeamMatches, setModalTeamMatches] = useState<TeamMatch[]>([]);
  const [modalPlayerMatches, setModalPlayerMatches] = useState<PlayerMatch[]>([]);
  useEffect(() => {
    setModalTeamMatches(teamMatches.filter(teamMatch => !!teamMatch.teams.some(t => t.xref === modalTeam)));
    setModalPlayerMatches(
      playerMatches.filter(playerMatch => playerMatch.players.some(p => p.team.xref === modalTeam)),
    );
  }, [modalTeam]);

  return (
    <>
      <Leagues>
        {season.nights.map(night => (
          <Divisions key={night}>
            <H2 style={{ marginBottom: '1rem' }}>{dayOfWeek(night)}</H2>
            {season.divisions[night]!.map(division => (
              <Division key={division}>
                {teamsByDivision[division] && (
                  <>
                    <H3>{division}</H3>
                    <Table
                      data={teamsByDivision[division]}
                      columns={[
                        <TableHeader label="Place" />,
                        <TableHeader label="Team Name" />,
                        <TableHeader label="W's" />,
                        <TableHeader label="L's" />,
                        <TableHeader label="PCT." />,
                      ]}
                    >
                      {({ datum: ts }) => (
                        <Row key={ts.xref}>
                          <Cell>{ts.rank}</Cell>
                          <Cell>
                            <Button
                              style={{ backgroundColor: '#ffffff', border: 0, color: theme.colors.lg.blue.base }}
                              onClick={() => setModalTeam(ts.xref)}
                            >
                              {ts.name}
                            </Button>
                          </Cell>
                          <Cell>{ts.wins}</Cell>
                          <Cell>{ts.losses}</Cell>
                          <Cell>{ts.pct.toFixed(3)}</Cell>
                        </Row>
                      )}
                    </Table>
                  </>
                )}
              </Division>
            ))}
          </Divisions>
        ))}
      </Leagues>
      <TeamResults
        open={!!modalTeam}
        playerMatches={modalPlayerMatches}
        team={teamsByXref[modalTeam]}
        teamMatches={modalTeamMatches}
        setOpen={open => {
          if (!open) {
            setModalTeam(0);
          }
        }}
      />
    </>
  );
}

export default SeasonPage;
