import React, { useEffect, useState } from 'react';
import { Routes, Route } from 'react-router-dom';
import { useMediaQuery } from 'react-responsive';

import { useAtlas } from '../realm';
import { Player, Season, Team } from '../types';

import { LoginPage, ProtectedRoute } from './auth';
import Header from './header';
import MobileHeader from './header_mobile';
import HomePage from './home';
import PlayersPage from './players';
import Sandbox from './sandbox';
import SeasonsPage from './seasons';
import StandingsPage from './standings';
import TeamPage from './team';

function App() {
  const { data } = useAtlas();

  const [seasons, setSeasons] = useState<Season[]>([]);
  useEffect(() => {
    if (!data) {
      return;
    }
    data.seasons().find().then(setSeasons);
  }, [data]);

  const [teams, setTeams] = useState<Team[]>([]);
  useEffect(() => {
    if (!data) {
      return;
    }
    data.teams().find().then(setTeams);
  }, [data]);

  const [players, setPlayers] = useState<Player[]>([]);
  useEffect(() => {
    if (!data) {
      return;
    }
    data.players().find().then(setPlayers);
  }, [data]);

  const isMobile = useMediaQuery({ query: '(max-width: 720px)' });

  return (
    <>
      {isMobile ? <MobileHeader /> : <Header />}
      <Routes>
        <Route
          path="/"
          element={
            <ProtectedRoute>
              <HomePage />
            </ProtectedRoute>
          }
        />
        <Route
          path="seasons/*"
          element={
            <ProtectedRoute>
              <SeasonsPage />
            </ProtectedRoute>
          }
        />
        <Route
          path="/standings"
          element={
            <ProtectedRoute>
              <StandingsPage seasons={seasons} />
            </ProtectedRoute>
          }
        />
        <Route
          path="team"
          element={
            <ProtectedRoute>
              <TeamPage seasons={seasons} teams={teams} />
            </ProtectedRoute>
          }
        />
        <Route
          path="players"
          element={
            <ProtectedRoute>
              <PlayersPage seasons={seasons} teams={teams} players={players} />
            </ProtectedRoute>
          }
        />
        <Route path="login" element={<LoginPage />} />
        <Route path="sandbox" element={<Sandbox />} />
      </Routes>
    </>
  );
}

export default App;
